import { Component, OnInit } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { Observable } from 'rxjs';
import { DeliveryGoodsA4Component } from 'src/app/sales/components/print/delivery-goods/delivery-goods-a4/delivery-goods-a4.component';
import { DeliveryGoodsA5Component } from 'src/app/sales/components/print/delivery-goods/delivery-goods-a5/delivery-goods-a5.component';
import { DeliveryGoodsK57Col3Component } from 'src/app/sales/components/print/delivery-goods/delivery-goods-k57-col3/delivery-goods-k57-col3.component';
import { DeliveryGoodsK80Col3Component } from 'src/app/sales/components/print/delivery-goods/delivery-goods-k80-col3/delivery-goods-k80-col3.component';
import { DeliveryGoodsK80Col4Component } from 'src/app/sales/components/print/delivery-goods/delivery-goods-k80-col4/delivery-goods-k80-col4.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';

import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhBillType2930Add, VhBillType2930DetailAdd } from '../bill-type2930-interface';


@Component({
  selector: 'bill-type2930-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {
  display_promotion_selling_price: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price
  invoice: VhBillType2930Add = {
    bill_type: 29,
    date: new Date(),
    discount: 0,
    fee: 0,
    id_branch: this.vhQuerySales.getDefaultBranch()._id,
    id_branch_receive: '',
    id_employee: this.vhAuth.getUser()._id,
    note: '',
    payment: 0,
    payment_type: 2,
    tax: 0,
    total: 0,
    bill_code: '',

  }
  invoice_detail: VhBillType2930DetailAdd[] = []
  subTotal = 0;
  taxValue = 0
  listTax: any[] = this.vhQuerySales.getlocalTaxs().filter(tax => tax.status);
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore
  branchList: any = []
  employee: any = {};
  pickerTime = new Date();
  selected_branch: string = '';
  branchDefault: any;
  printer: any;
  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private lang: LanguageService,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private vhAuth: VhAuth,
    private fncService: FunctionService
  ) {
    this.employee = this.vhAuth.getlocalEmployee(this.invoice.id_employee);
    this.branchDefault = this.vhQuerySales.getDefaultBranch()
    this.branchList = this.vhQuerySales.getlocalBranchs().filter(item => item._id != this.branchDefault._id)
    // ------bt s kin nhn phm nng-----
    this.eventKeypress();
  }
  /**
   * bt sk thay i chi nhnh nhn hng
   * @param event id_branch_selected
   * @example this.changeBranch('DNIWUJNHDKIJWHDIU')
   */
  changeBranch(event) {
    this.selected_branch = event
    this.invoice.id_branch_receive = (event)
  }

  ngOnInit(): void {
    let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_delivery_receive_goods')
    this.printer = printer ? printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)] : { discount_hidden: false, note_hidden: false, fee_hidden: false, tax_hidden: false, debt_hidden: false };
    this.checkPrinter = this.printer.enable;
    let tax = this.listTax.find(item => item.default);
    if (tax) {
      this.id_tax = tax._id
      this.invoice.tax = (tax['value']);
    }


  }
  event: any // bin lng nghe
  ngOnDestroy() {
    document.removeEventListener('keydown', this.event, false);
  }
  checkPrinter: boolean = false;

  /**
   * lng nghe sk nhn f1 f2 f3 f4 trn bn phm
   * @example 
   */
  eventKeypress() {
    this.event = (event: any) => {
      const keycode = event.keyCode;
      if (keycode === 112 || keycode === 113) event.preventDefault();
      if (keycode === 115) this.searchProductList = !this.searchProductList;
      else if (this.invoice_detail.length > 0) {
        switch (keycode) {
          case 112: this.payInvoice(29, false)
            break;
          case 113: {
            if (this.checkPrinter) this.payInvoice(29, true)
            break;
          }
        }
      }
    }
    document.addEventListener('keydown', this.event, false);
  }
  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      if (document.querySelector(".discount-price")) this.discount_price = this.vhAlgorithm.vhnumeral(".discount-price");
      if (document.querySelector(".invoice-fee")) this.invoice_fee = this.vhAlgorithm.vhnumeral(".invoice-fee")
    })

  }
  /**
   *  hm xa sp trong bill_detail
   * @param detail
   * @example  this.deleteGoods(bill_detail_selected)
   */
  deleteGoods(detail: VhBillType2930DetailAdd) {
    this.vhComponent.alertConfirm("", "", `${this.lang.translate("Delete")} ${detail.name}?`, "OK", this.lang.translate("Cancel"))
      .then(() => this.removeInvoiceDetail(detail.id_product), () => { })
  }
  searchProductList: boolean = false;
  /**
  * m component chn sp
  * @example this.openSearchProductList()
  */
  openSearchProductList() {
    this.searchProductList = true;
  }
  /**
   * nhn output t component chn sp (app-search-product)
   * @param data sp c chn  thm vo bill
   * @example this.closeSearchProductList(product_selected)
   */
  closeSearchProductList(data) {
    if (data) {
      if (data.manylot && !data.lot_number) this.openModalChooseLot(data)
      else
        if (!this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').show_product_mutil_line_delivery_goods)
          this.addInvoiceDetail(data);
        else
          this.addBill_Detail(data);
    }

    this.searchProductList = false;
  }
  /**
   * tr v trang giao hng
   * @example this.goBack()
   */
  goBack() {
    this.vhComponent.alertConfirm(`${this.lang.translate("Confirm")}`, "", `${this.lang.translate("Are you sure to cancel this delivery note")} ?`, "OK", this.lang.translate('Cancel'))
      .then(() => { this.isExit = true; this.router.navigate(["/sales/dashboard/manage/stock/delivery"], { state: this.dataRestore }) }, () => { })
  }

  /**
   * bt sk thay i s lng sp trong bill
   * @param product 
   * @param event : gi tr thay i
   * @example this.checkQuantityProduct(bill_detail_selected, 1)
   */
  checkQuantityProduct(product: VhBillType2930DetailAdd, event) {
    if (!event) product.quantity = 1;
    this.setSubTotal();
  }

  /**
   * bt sk thay i n v ca bill_detail
   * @param ev  ratio c chn
   * @param item
   * @example  this.changeUnit(24, bill_detail_selected)
   */
  changeUnit(ev, item) {
    let unit = this.vhQuerySales.getUnit_byRatio(item.units, ev);
    if (unit) {
      item.unit = unit.unit
      item.price = parseFloat(unit.price)
      item.ratio = parseFloat(unit.ratio)
      item.price_origin = parseFloat(unit.price)
    }
    this.setSubTotal()
  }

  // -------modal edit unit price-------
  isVisibleEditUnitPrice: boolean = false;
  unit_price: any;
  tempDetail: VhBillType2930DetailAdd;
  /**
   * m modal thay i gi bn sn phm trong bill
   * @param detail 
   * @example this.openEditUnitPrice(bilL_detail_selected)
   */
  openEditUnitPrice(detail: VhBillType2930DetailAdd) {
    this.tempDetail = detail;
    this.isVisibleEditUnitPrice = true;
    this.vhAlgorithm.waitingStack().then(() => {
      this.unit_price = this.vhAlgorithm.vhnumeral('.unit_price');
    });
  }
  /**
   * bt sk nhn nt Ok modal gim gi sp
   * @example this.handleEditUnit()
   */
  handleEditUnit() {
    if (this.unit_price.getRawValue() != '' && this.unit_price.getRawValue() >= 0) {
      this.tempDetail.price = parseFloat(this.unit_price.getRawValue());
      this.setSubTotal();
      this.isVisibleEditUnitPrice = false;
    } else { this.vhComponent.showToast(2000, this.lang.translate("Invalid data, please check again"), "alert-toast") }
  }
  /**
   * tt modal gim gi sp
   * @example this.handleCancelEditUnit()
   */
  handleCancelEditUnit() { this.isVisibleEditUnitPrice = false }
  // -------set employee-------
  /**
   * bt sk thay i nv bn hng
   * @param event id_employee selected
   */
  closeAndReceiveEmployee(event) {
    if (event) {
      this.invoice.id_employee = (event);
    }
  }
  /**
   * bt sk thay i date ca ha n , gn li gi tr date mi cho n
   * @example this.setPickerTime('2023-06-12T08:02:27.697Z')
   */
  setPickerTime(event) {
    if (new Date(event).getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime()) {
      this.vhComponent.alertMessageDesktop("warning", `${this.lang.translate("You can only create or edit the invoice from the registration date (")} ${this.fncService.formatDate(this.vhAuth.getStore().registrationdate)} ${")"}`, 5000)
      this.pickerTime = new Date(this.invoice.date)
      this.invoice.date = (new Date(this.pickerTime));
    }
    else this.vhQuerySales.getValidityDate(new Date(event)).then((validitydate: any) => {
      if (validitydate == null) {
        this.invoice.date = (event);
      }
      else {
        this.pickerTime = new Date(validitydate)
        this.invoice.date = (new Date(this.pickerTime));
        this.vhComponent.alertMessageDesktop("warning", `${this.lang.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 5000)
      }
    })
  }
  // -------change tax------
  id_tax: string;
  /**
   * bt sk thay i thu ca bill
   * @param event 
   */
  changeTax(event) {
    let tax = this.listTax.find(item => item._id == event);
    this.invoice.tax = (tax.value);
    this.setTaxValue();
  }
  discount_price: any;
  /**
   * bt sk thay i gim gi ca ha n , gn li gi tr gim gi mi cho n
   * @example this.editDiscount()
   */
  editDiscount() {
    if (this.discount_price.getRawValue() && this.discount_price.getRawValue() >= 0) {
      this.invoice.discount = (parseFloat(this.discount_price.getRawValue()));
    } else {
      document.getElementById("discount-price")['value'] = this.invoice.discount;
      this.discount_price = this.vhAlgorithm.vhnumeral('.discount-price');
    }
    this.setTotal()
  }
  invoice_fee: any;
  /**
   * bt sk thay i ph ca ha n , gn li gi tr ph mi cho n
   * @example this.editFee()
   */
  editFee() {
    if (this.invoice_fee.getRawValue() && this.invoice_fee.getRawValue() >= 0) {
      this.invoice.fee = (parseFloat(this.invoice_fee.getRawValue()));
    } else {
      document.getElementById("invoice-fee")['value'] = this.invoice.fee;
      this.invoice_fee = this.vhAlgorithm.vhnumeral('.invoice-fee');
    }
    this.setTotal()
  }

  /**
   * tr v trang danh sch ha n sau khi  thanh ton xong
   * @example this.handle(true)
   */
  handle() {
    this.isExit = true;
    this.router.navigate(['/sales/dashboard/manage/stock/delivery'], { state: this.dataRestore })
  }
  note: string;
  /**
   * bt sk thay i note ca bill
   * @example this.edit_note()
   */
  edit_note() {
    if (this.note) {
      this.invoice.note = (this.note)
    }
  }
  save_enable = true;
  /**
    * x l sk nhn nt thanh ton hoc lu tm
    * @param bill_type 
    * @param print bin c  check in hay ko
    * @example this.payInvoice(1)
    */
  payInvoice(bill_type: 29, print?) {
    if (this.invoice.id_branch_receive) {
      this.vhComponent.alertConfirm(
        `${this.lang.translate("Delivery goods")} ?`,
        "", "", "OK", this.lang.translate("Cancel")
      ).then(() => {
        if (this.save_enable) {
          this.save_enable = false;
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            var arrayProcess: any[] = [this.createOrUpdate(bill_type)];
            Promise.all(arrayProcess)
              .then((bill) => {
                this.save_enable = true;
                this.vhComponent.hideLoading(0).then(() => {
                  this.vhComponent.showToast(2000, `${this.lang.translate("Delivery note")} ${this.lang.translate("has been updated successfully")}`, "success-toast")
                  if (print) this.handlePrint()
                  else this.handle()
                })
              })
          }).catch(err => { this.vhComponent.hideLoading(0) ;this.save_enable = true;})
        }
      }, () => { })
    } else this.vhComponent.showToast(2000, this.lang.translate("Please select a branch"), "alert-toast")
  }
  /**
   * to bill ln DB
   * @param bill_type 
   * @returns 
   */
  createOrUpdate(bill_type: 29) {
    return this.vhQuerySales.createBill_Billdetail(this.invoice, this.getCreateUpdateInvoiceDetail())
  }

  /**
  * ly thng tin d liu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_delivery_receive_goods')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }
  /**
   * kim tra d liu thng tin my in v m component in
   * @example this.handlePrint()
   */
  handlePrint() {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer, invoice: this.invoice, invoice_detail: this.invoice_detail }, false, false, `modal-print-${printer['_id']}`).then((modal) => {
          modal.onWillDismiss().then(() => this.handle())
        })
    }, (err) => this.handle())
  }
  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return DeliveryGoodsK57Col3Component
      case 'print_size_k80_3c': return DeliveryGoodsK80Col3Component
      case 'print_size_k80_4c': return DeliveryGoodsK80Col4Component
      case 'print_size_a5': return DeliveryGoodsA5Component
      case 'print_size_a4': return DeliveryGoodsA4Component
      default: return DeliveryGoodsA4Component
    }
  }
  // ------thot trang-----
  isExit: boolean = false;
  checkDeactivate(currentRoute: ActivatedRouteSnapshot, currentState: RouterStateSnapshot, nextState?: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return nextState.url == '/login' ? true : (this.isExit ? this.isExit : this.vhComponent.alertConfirm(this.lang.translate("Confirm"), "", this.lang.translate("Are you sure to cancel the sales order"), "OK", (this.lang.translate("Cancel"))).then(() => { return true }).catch(() => { return false }))
  }


  show_modal_select_lots = false;
  list_search_lot = [];

  /**
   * tr v tn l theo id_lot c chn
   * @param lots 
   * @param _id 
   * @returns string
   * @example this.getLotNumber([], 'DJWOIDHIEUGHWDSJDNWIHD')
   */
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id);
    return lot.lot_number
  }
  /**
   * lc danh sch l ca sp theo s l (lot_number)
   * @param value 
   */
  searchLot(value) {
    this.tempDetail.lots = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value), this.list_search_lot, ['lot_number'])
  }
  /**
   * m modal chn l cho sp
   * @param data 
   * @example this.openModalChooseLot(bill_detail_selected)
   */
  openModalChooseLot(data) {
    this.tempDetail = data
    this.list_search_lot = this.tempDetail.lots
    if (!this.list_search_lot.length) this.vhComponent.alertMessageDesktop("error", this.lang.translate("Sn phm ny khng c l kh dng  bn!"))
    else this.show_modal_select_lots = true;
  }

  /**
   * bt sk thay i id_lotnumber ca bill-detail
   * @param e id_lotnumber thay i
   * @example this.changeLot('DNIUHWIDCHBIQOHSDOIHW')
   */
  changeLot(e) {
    this.tempDetail.lot_number = this.tempDetail.lots.find(item => item._id == e).lot_number
  }

  /**
   * bt sk nhn nt ok ca modal chn l sp
   * @example this.handleChooseLot()
   */
  handleChooseLot() {
    

    this.show_modal_select_lots = false;
    this.tempDetail.lots = this.list_search_lot;
    if (!this.tempDetail.id_product) this.closeSearchProductList(this.tempDetail);
  }

  /**
   * bt sk nhn nt hy modal chn l sp
   * @example this.handleCancelChooseLot()
   */
  handleCancelChooseLot() {
    this.show_modal_select_lots = false;
    this.tempDetail.lots = this.list_search_lot
  }

  /**
   * thm sn phm c chn t drawer search-product
   * @param value 
   * @example this.addInvoiceDetail(product_selected)
   */
  addInvoiceDetail(value,) {
    if (value['id_subproduct']) {
      if (value.lot_number) {
        let index = this.invoice_detail.findIndex(detail => detail['lot_number'] == value['lot_number'] && detail['id_lotproduct'] == value['id_lotproduct'])
        if (index != -1) {
          if (this.invoice_detail[index]['quantity'] < 9999999999) this.invoice_detail[index]['quantity'] += 1
          this.setSubTotal()
        } else {
          this.addBill_Detail(value)
        }
      }
      else {
        let index = this.invoice_detail.findIndex(detail => detail['id_subproduct'] == value['id_subproduct'] && detail['ratio'] == value['ratio'])
        if (index != -1) {
          if (this.invoice_detail[index]['quantity'] < 9999999999) this.invoice_detail[index]['quantity'] += 1
          this.setSubTotal()
        } else {
          this.addBill_Detail(value)
        }
      }
    }
    else {
      if (value.lot_number) {
        let index = this.invoice_detail.findIndex(detail => detail['lot_number'] == value['lot_number'] && detail['id_lotproduct'] == value['id_lotproduct'])
        if (index != -1) {
          if (this.invoice_detail[index]['quantity'] < 9999999999) this.invoice_detail[index]['quantity'] += 1
          this.setSubTotal()
        } else {
          this.addBill_Detail(value)
        }
      }
      else {
        let index = this.invoice_detail.findIndex(detail => detail['id_product'] == value['_id'] && detail['ratio'] == value['ratio'])
        if (index != -1) {
          if (this.invoice_detail[index]['quantity'] < 9999999999) this.invoice_detail[index]['quantity'] += 1
          this.setSubTotal()
        } else {
          this.addBill_Detail(value)
        }
      }
    }
  }
  /**
   * x l cc trng ca sp c chn v thm vo mng local
   * @param value 
   * @param new_price 
   * @example  this.addBill_Detail(product_selected)
   */
  addBill_Detail(product) {
    let detail = {
      id_product: product._id,
      quantity: 1,
      price: product.price_import,
      price_origin: product.price_import,
      unit: product.unit,
      units: product.units,
      ratio: product.ratio ? product.ratio : 1,
      ptype: 1,
      name: product.name,
      id_subproduct: product.id_subproduct
    }

    if (product.lot_number) detail['lot_number'] = product.lot_number;
    if (product.lots) detail['lots'] = product.lots;
    if (product.id_lotproduct) detail['id_lotproduct'] = product.id_lotproduct;
    if (product.id_subproduct) detail['id_subproduct'] = product.id_subproduct;

    this.invoice_detail.push(detail)

    this.setSubTotal()
  }
  /**
   * xa 1 bill_detail trong local
   * @param id id_bill_detail selected
   * @param notSetSub 
   */
  removeInvoiceDetail(id: string) {
    let index = this.invoice_detail.findIndex(detail => detail['id_product'] == id)
    if (index != -1) {
      this.invoice_detail.splice(index, 1);
      this.setSubTotal()
    }
  }
  /**
   * tnh li cng tin ca bill khi c s thay i (thm xa sa bill_detail, i gi bn, khuyn mi, ph ,thu ,...)
   * @example this.setSubTotal()
   */
  setSubTotal() {
    this.subTotal = this.invoice_detail.reduce((prev: number, next) => prev + next.quantity * next.price, 0)
    this.setTotal()
  }
  /**
   * tnh li tng tin ca bill  lu trng total
   * @example this.setTotal();
   */
  setTotal() {
    this.invoice.total = this.subTotal - this.invoice.discount + this.invoice.fee
    this.setTaxValue()
  }
  /**
   * tnh gi tr thu ra tin
   * @example this.setTaxValue()
   */
  setTaxValue() {
    this.taxValue = this.invoice.total * (this.invoice.tax / 100)
  }
  /**
   * tr v tng tin 
   * @returns number
   * @example let total = this.getTotalATax()
   */
  getTotalATax(): number {
    return this.invoice.total + this.taxValue
  }
  /**
   * tr v d liu thng tin bill_detail clean  lu DB
   * @returns array[object]
   *  @example let bill_details = this.getCreateUpdateInvoiceDetail()
   */
  public getCreateUpdateInvoiceDetail(): VhBillType2930DetailAdd[] {
    return this.invoice_detail.map(item => {
      let detail: VhBillType2930DetailAdd = {
        id_product: item.id_product,
        price: item.price,
        quantity: item.quantity,
        ptype: item.ptype,
        ratio: item.ratio ? item.ratio : 1,
      }
      if (item.id_lotproduct) detail['id_lotproduct'] = item.id_lotproduct
      if (item.id_subproduct) detail['id_subproduct'] = item.id_subproduct
      if (item.price < item.price_origin) detail['price_origin'] = item.price_origin
      return detail
    })
  }
}
