
import { ChangeDetectorRef, Component } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { DeliveryGoodsA4Component } from 'src/app/sales/components/print/delivery-goods/delivery-goods-a4/delivery-goods-a4.component';
import { DeliveryGoodsA5Component } from 'src/app/sales/components/print/delivery-goods/delivery-goods-a5/delivery-goods-a5.component';
import { DeliveryGoodsK57Col3Component } from 'src/app/sales/components/print/delivery-goods/delivery-goods-k57-col3/delivery-goods-k57-col3.component';
import { DeliveryGoodsK80Col3Component } from 'src/app/sales/components/print/delivery-goods/delivery-goods-k80-col3/delivery-goods-k80-col3.component';
import { DeliveryGoodsK80Col4Component } from 'src/app/sales/components/print/delivery-goods/delivery-goods-k80-col4/delivery-goods-k80-col4.component';

import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhBillType2930Edit, VhBillType2930DetailEdit, } from '../bill-type2930-interface';

@Component({
  selector: 'bill-type2930-detail',
  templateUrl: './dettail.component.html',
  styleUrls: ['./dettail.component.scss']
})
export class DettailComponent {
  display_promotion_selling_price: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price
  printer: any;
  tableHeight: string;
  public invoiceInfo: any;
  invoice_detail: VhBillType2930DetailEdit[] = []
  subTotal = 0;
  public invoice: VhBillType2930Edit
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore
  branchList: any = this.vhQuerySales.getlocalBranchs().filter(item => item._id != this.vhQuerySales.getDefaultBranch()._id)
  billInfo: any = this.router.getCurrentNavigation().extras.state.billInfo
  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
    private lang: LanguageService,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private fncService: FunctionService
  ) {
    this.getDataInvoice(this.billInfo._id)

  }

  /**
   * gn bill v bill-detail get c vo bin v get cc thng tin ca nv, khch hng, phng thc thanh ton
   * @example this.init(bill, bill-deails)
   */
  init(invoice: VhBillType2930Edit = null, invoiceDetail: VhBillType2930DetailEdit[] = null) {
    this.invoice = invoice
    this.invoice_detail = invoiceDetail
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.id_employee),
      }
    } else this.invoiceInfo = { employee: {} }
    this.setSubTotal()
  }
  /**
   * tnh cng tin ha n
   * @example this.setSubTotal()
   */
  setSubTotal() {
    let subTotal = 0;
    for (let i of this.invoice_detail) {
      subTotal += (i.price ? i.price * i.quantity : 0)
    }
    this.subTotal = subTotal;
  }

  /**
   * get d liu bill t DB
   * @param id : nhn t state truyn qua
   * @example this.getDataInvoice('DHIWHNXDIWODUHIWDHOWID')
   */
  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQuerySales.getBill(id),
        this.vhQuerySales.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        if (!invoiceDetail.length)
          this.init(invoice, [])
        else {
          invoiceDetail = this.vhAlgorithm.sortVietnamesebyASC(invoiceDetail, 'name');
          this.init(invoice, invoiceDetail)
        }
        this.vhComponent.hideLoading(0)
      })
    })

  }


  /**
   * tr v tn unit ca sn phm theo ratio truyn vo
   * @param units 
   * @param ratio 
   * @returns string
   * @example let unit_name = this.getUnit([object], 1)
   */
  getUnit(units: any[], ratio) {
    return this.vhQuerySales.getUnit_byRatio(units, ratio).unit
  }

  /**
   * tr v chui thng tin l theo id l truyn vo
   * @param lots 
   * @param _id 
   * @returns string
   * @example let lot = this.getLotNumber([], 'DNIWUHNDKLJWHDIWUOH')
   */
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id)
    return lot.lot_number + '\n' + this.fncService.formatDate(lot.date_mfg) + ' - ' + this.fncService.formatDate(lot.date_exp)
  }
  /**
   * xa ha n ln DB
   * @example this.deleteInvoice()
   */
  deleteInvoice() {
    this.vhQuerySales.getValidityDate(this.invoice.date).then((validitydate: any) => {
      if (validitydate == null) {
        this.vhComponent.alertConfirm(this.lang.translate("Confirm"), "", `${this.lang.translate("Delete")}?`, "OK", this.lang.translate("Cancel"))
          .then(() => {
            this.vhComponent.showLoading("", "transparent-loading").then(() => {
              this.vhQuerySales.deleteBill_Billdetail(this.invoice._id).then(() => {
                this.vhComponent.hideLoading(0).then(() => {
                  this.vhComponent.showToast(2000,
                    `${this.lang.translate("Delivery goods")} ${this.invoice.bill_code} ${this.lang.translate("has been cancelled successfully")}`, "success-toast")
                  this.router.navigate(['/sales/dashboard/manage/stock/delivery'])
                })
              })
            })
          }, () => { })
      }
      else {
        this.vhComponent.alertMessageDesktop("warning", `${this.lang.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 5000)
      }
    })

  }
  public goToCart() {
    this.vhQuerySales.getValidityDate(this.invoice.date).then((validitydate: any) => {
      if (validitydate == null) {
        this.router.navigate(['/sales/dashboard/manage/stock/delivery/edit'], { state: { invoiceDetail: this.invoice_detail, invoice: this.invoice, dataRestore: this.dataRestore, branchList: this.branchList } })
      }
      else {
        this.vhComponent.alertMessageDesktop("warning", `${this.lang.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 5000)
      }
    })

  }
  /**
  * ly thng tin d liu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_delivery_receive_goods')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  /**
   * kim tra thng tin d liu trang in v m component in
   * @example this.printInvoice()
   */
  printInvoice() {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer, invoice: this.invoice, invoice_detail: this.invoice_detail }, false, false, `modal-print-${printer['_id']}`)
    }, (err) => {
      this.vhComponent.alertMessage(this.lang.translate("Function"), "", this.lang.translate("Please turn on the printer function"), "OK")

    })
  }

  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return DeliveryGoodsK57Col3Component
      case 'print_size_k80_3c': return DeliveryGoodsK80Col3Component
      case 'print_size_k80_4c': return DeliveryGoodsK80Col4Component
      case 'print_size_a5': return DeliveryGoodsA5Component
      case 'print_size_a4': return DeliveryGoodsA4Component
      default: return DeliveryGoodsA4Component
    }
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/stock/delivery'], { state: this.dataRestore })
  }
  ngAfterViewChecked() {
    if (document.querySelector("#delivery-good-detail-invoice") && document.querySelector(".ant-table-thead") && document.querySelector(".delivery-good-detail-invoice-header") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.querySelector("#delivery-good-detail-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".delivery-good-detail-invoice-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 40 + "px";
    }
    this.cdRef.detectChanges();
  }
}
