import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CarryingTradeComponent } from './carrying-trade.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AddComponent } from './add/add.component';
import { DetailComponent } from './detail/detail.component';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { CanLeavePage } from 'src/app/canLeavePage';
import { BillType2930Module } from 'src/app/sales/components/bill-type/bill-type-29-30/bill-type-29-30.module';
import { EditComponent } from './edit/edit.component';
const routes: Routes = [
  {
    path: "",
    component: CarryingTradeComponent
  },
  {
    path: "add",
    component: AddComponent,
   
  },
  {
    path: "detail",
    component: DetailComponent,
  },
  {
    path: "edit",
    component: EditComponent,
  }
]


@NgModule({
  declarations: [CarryingTradeComponent, AddComponent, DetailComponent, EditComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    ScrollingModule,
    BillType2930Module
  ]
})
export class CarryingTradeModule { }
