import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhDeliveryGood } from 'src/app/sales/interface/vh-delivey-goods';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-carrying-trade',
  templateUrl: './carrying-trade.component.html',
  styleUrls: ['./carrying-trade.component.scss']
})
export class CarryingTradeComponent implements OnInit {
  public date: Date[] = [new Date(), new Date()];
  public searchValue: string = ''
  public invoiceShow: VhDeliveryGood[] = []
  public invoice: VhDeliveryGood[];
  dataRestore: any = this.router.getCurrentNavigation().extras.state
  tableHeight: string;
  branchList: Array<any> = []
  loadingStatus: boolean = false
  constructor(
    private vhQuery: VhQuery,
    private router: Router,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private lang: LanguageService,
    private cdRef: ChangeDetectorRef,
    private fncService: FunctionService
  ) {
  }

  ngOnInit() {
    if (this.dataRestore) {
      this.searchValue = this.dataRestore.searchValue
      this.date = [new Date(this.dataRestore.startTime), new Date(this.dataRestore.endTime)];
      this.invoice = this.dataRestore.ininvoice
      this.getInvoice()
    }
    // this.getInvoice()
  }
  /**
   * lc bill theo bill_code
   */
  searchBill(value: string) {
    if (value && value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.invoiceShow = this.vhAlgorithm.searchList(val, this.invoice, ['bill_code'])
    } else this.invoiceShow = [...this.invoice]
  }
  /**
   * get danh sch ha n chuyn hng bill-type 29 30
   */
  getInvoice() {
    this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => {
      Promise.all(
        [this.vhQuerySales.getBills_byFields( { date: { $gte: new Date(this.date[0].setHours(0, 0, 0)), $lte: new Date(this.date[1].setHours(23, 59, 59, 59)) }, bill_type: { $in: [29, 30] }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id } }, {}, {}, 0)]).then((res: any) => {
          this.vhComponent.hideLoading(0).then(() => {
            this.branchList = this.vhQuerySales.getlocalBranchs()
            this.invoice = res[0].map(item => {
              return {
                ...item,
                branch_name: this.checkBranchName(item.id_branch),
                branch_name_reception: this.checkBranchName(item.id_branch_receive),
                status: this.checkBillType(item.bill_type),
                total: item.total *(1+ item.tax /100)
              }
            }).reverse()
            this.loadingStatus = true
            this.invoiceShow = [...this.invoice]
            
            this.searchBill(this.searchValue)
          })
        }).catch(err => { this.vhComponent.hideLoading(0); console.error(err) })
    })
  }
  /**
   * tr v tn loi ha n theo bill_type
   * @example let status = this.checkBillType(29)
   */
  checkBillType(bill_type) {
    //kim tra loi ha n
    switch (bill_type) {
      case 29: { return (this.lang.translate("Delivering goods")); }
      case 30: { return (this.lang.translate("Delivered goods")); }
    }
  }
  /**
   * tr v tn chi nhnh theo id_branch truyn vo
   */
  checkBranchName(id) {
    return this.vhQuerySales.getlocalBranch(id).name
  }
  goToCart() {
    let dataRestore = { searchValue: this.searchValue, startTime: this.date[0], endTime: this.date[1], invoice: this.invoice }
    this.router.navigate(["/sales/dashboard/manage/stock/delivery/add"], { state: { dataRestore, branchList: this.branchList } })
  }

  goToDetail(bill) {
    let dataRestore = { searchValue: this.searchValue, startTime: this.date[0], endTime: this.date[1], invoice: this.invoice }
    this.router.navigate(["/sales/dashboard/manage/stock/delivery/detail"], { state: { billInfo: bill, dataRestore, branchList: this.branchList } })
  }
  onCurrentPageDataChange(event) { }
  ngAfterViewChecked() {
    if (document.querySelector("#delivery-goods") && document.querySelector(".ant-table-thead") && document.querySelector(".delivery-goods-header")) {
      this.tableHeight = document.querySelector("#delivery-goods").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".delivery-goods-header").clientHeight - 60 + "px";
      if (document.querySelector(".ant-table-pagination")) {
        this.tableHeight = document.querySelector("#delivery-goods").clientHeight - document.querySelector(".ant-table-thead").clientHeight
          - document.querySelector(".delivery-goods-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 60 + "px";
      }
    }
    this.cdRef.detectChanges();
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/stock"])
  }

  /**
   * bt sk thay i thi gian xem bo co
   * @param event 
   * @example this.changeTime(['2023-06-27T01:21:41.397Z','2023-06-27T01:21:41.397Z'])
   */
  changeTime(event) {
    if (new Date(event[1].setHours(0,0,0,0)).getTime() - new Date(event[0].setHours(0,0,0,0)).getTime() <=  this.fncService.checkDateReport() * 86000000) {
      //this.getExpenses(new Date(this.date[0].setHours(0, 0, 0, 0)), new Date(this.date[1].setHours(23, 59, 59, 59)));
    } else {
      this.vhComponent.alertMessageDesktop("error", this.lang.translate("The time limit to view is") + " " + this.fncService.checkDateReport() + " " +  this.lang.translate("days"))
      this.date = [new Date(event[0]), new Date(event[0].getTime() + this.fncService.checkDateReport() * 86000000)]
    }
  }


  /** hm sort cho cc ct */
  dateCol = false;
  bill_codeCol = false;
  branch_nameCol = false;
  branch_name_receptionCol = false;
  payment_nameCol = false;
  statusCol = false;
  totalCol = false;
  /** Hm thc hin sp xp theo collect
   *
   * @param colName       // tn ct mun sp xp
   */
  sortTable(colName){
    switch (colName) {
      case 'date':
        if(this.dateCol){
          this.invoiceShow = this.vhAlgorithm.sortDatebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortDatebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'bill_code':
        if(this.bill_codeCol){
          this.invoiceShow = this.vhAlgorithm.sortStringbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortStringbyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'branch_name':
        if(this.branch_nameCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'branch_name_reception':
        if(this.branch_name_receptionCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'payment_name':
        if(this.payment_nameCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'status':
        if(this.statusCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'total':
        if(this.totalCol){
          this.invoiceShow = this.vhAlgorithm.sortNumberbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortNumberbyDESC([...this.invoiceShow], colName);
        }
        break;
    }
  }
}
